<template>
  <div class="wallet" v-if="userinfo">
    <div class="wrap">
      <div class="top">
        <div class="left">
          <img src="../assets/logo.png" alt />
          <span>UORA</span>
        </div>
        <div class="right" @click="getmoney" v-if="CanGetAmount>0">{{$t('walle.klq')}}</div>
      </div>
      <div class="num">
        <span>{{$t('walle.ylq')}}</span>
        <span class="numdetail">{{(userinfo.gottenAmount/1000000000000000000).toFixed(6)}}</span>
        <span>UORA</span>
      </div>
      <div class="mid">
        <div class="item">
          <div class="left">{{userinfo.uoraAmount/1000000000000000000}}</div>
          <div class="right">{{$t('home.rgsl')}}</div>
        </div>
        <div class="item">
          <div class="left">{{UnlockedAmount/1000000000000000000}}</div>
          <div class="right">{{$t('walle.yjs')}}</div>
        </div>
        <div class="item">
          <div class="left">{{CanGetAmount/1000000000000000000}}</div>
          <div class="right">{{$t('walle.wlq')}}</div>
        </div>
        <div class="item">
          <div class="left">{{(userinfo.uoraAmount-UnlockedAmount)/1000000000000000000}}</div>
          <div class="right">{{$t('walle.wjs')}}</div>
        </div>
        <div class="item">
          <div class="left">{{syday}}</div>
          <div class="right">{{$t('walle.sfts')}}</div>
        </div>
      </div>
      <div class="bot">
        <div class="item" @click="select(0)">
          <img src="../assets/wl.png" alt />
          <span>{{$t('walle.dh')}} UORA</span>
        </div>
        <div class="item" @click="select(1)">
          <img src="../assets/wr.png" alt />
          <span>{{$t('walle.dh')}} USDT</span>
        </div>
      </div>
    </div>
    <div class="wrapbot">
      <div class="top">
        <div class="left">{{$t('walle.sqjl')}}</div>
        <div class="right" @click="getsociamoney" v-if="canGetCom>0">{{$t('walle.klq')}}</div>
      </div>
      <div class="bot">
        <div class="item">
          <div class="num">1000000.00</div>
          <div class="text">{{$t('walle.rgsl')}}</div>
        </div>
        <div class="item">
          <div class="num">{{comUnlocked/1000000000000000000}}</div>
          <div class="text">{{$t('walle.yjs')}}</div>
        </div>
        <div class="item">
          <div class="num">{{userinfo.gottenCom/1000000000000000000}}</div>
          <div class="text">{{$t('walle.ylq')}}</div>
        </div>
        <div class="item">
          <div class="num">{{canGetCom /1000000000000000000}}</div>
          <div class="text">{{$t('walle.wlq')}}</div>
        </div>
        <div class="item">
          <div class="num">{{1000000-(comUnlocked/1000000000000000000)}}</div>
          <div class="text">{{$t('walle.wjs')}}</div>
        </div>
        <div class="item">
          <div class="num">{{comLeftDays}}</div>
          <div class="text">{{$t('walle.syts')}}</div>
        </div>
      </div>
    </div>
    <div class="showbox" v-if="showbox" @click="showbox=false"></div>
    <div class="wrapbox" v-if="showbox">
      <div class="name">{{$t('walle.dh')}}{{type==0?'UORA':'USDT'}}</div>
      <div class="input">
        <input type="number" v-model="num" :placeholder="type==0?$t('walle.uora'):$t('walle.usdt')" />
      </div>
      <div class="input">{{$t('walle.sxf')}}:{{num*0.005}}</div>
      <div class="input">{{$t('walle.sjdz')}}:{{num*0.995}}</div>
      <div class="sure" @click="sure">{{$t('walle.qr')}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "wallet",
  data() {
    return {
      showbox: false,
      type: 0,
      syday: 0,
      comLeftDays: 0,
      comUnlocked: 0,
      canGetCom: 0,
      CanGetAmount: 0,
      UnlockedAmount: 0,
      userinfo: "",
      show: false,
      num: 0,
         heyue_addr:'0xf0e88e6a1e60df6e25fc58432447811020a503db',
      exchange:false
    };
  },
  mounted() {
    this.$toast.loading({
      message: this.$t("common.jzz"),
      forbidClick: true,
      duration: 0
    });
    if (Contract) {
      this.getpost();
    } else {
      window.addEventListener("load", async () => {
        if (window.ethereum) {
          window.web3 = new Web3(ethereum);
          try {
            await ethereum.enable();
          } catch (error) {}
        } else if (window.web3) {
          window.web3 = new Web3(web3.currentProvider);
        } else {
          console.log(
            "Non-Ethereum browser detected. You should consider trying MetaMask!"
          );
          window.web3 = new Web3(
            new Web3.providers.HttpProvider(
              "https://api.infura.io/v1/jsonrpc/ropsten"
            )
          );
        }
        web3.eth.getAccounts(function(err,accounts) {
          sessionStorage.setItem("token", accounts);
        });
        let heyue_addr = this.heyue_addr;
        let usdt_heyue = "0xee46026fb343831f0550ca10b5f36dabda34d120";
        let uora_heyue = "0x64eb9594009d2a0714a1313bc8010d94a04e3908";
        Contract = new web3.eth.Contract(abi, heyue_addr); //合约
        Contractusdttoken = new web3.eth.Contract(tokenabi, usdt_heyue); //合约
        Contractuoratoken = new web3.eth.Contract(tokenabi, uora_heyue); //合约
        this.getpost();
      });
    }
  },
  filters: {
    usdtformat(data) {
      return data / 1000000;
    },
    uoraformat(da) {
      return da / 1000000000000000000;
    }
  },
  methods: {
    getpost() {
      let a = sessionStorage.getItem("token");
      this.getuserinfo(a);
      this.getwallet(a); //剩余解锁天数
      this.getsqday();
      this.getcomUnlocked();
      this.getcanGetCom(a);
      this.getCanGetAmount(a);
      this.getUnlockedAmount(a);
      this.getexchange()
      setTimeout(() => {
        this.$toast.clear();
      }, 2000);
    },
    select(inx) {
      if(!this.exchange){
      this.$toast(this.$t('common.zwkf'))
      return
      }
      this.type = inx;
      this.showbox = true;
      
    },
    getuserinfo(accounts) {
      const that = this;
      Contract.methods
        .addressToUser(accounts)
        .call()
        .then(function(params) {
          console.log(params);
          that.userinfo = params;
        });
    },
    getwallet(a) {
      const that = this;
      Contract.methods
        .leftDays(a)
        .call()
        .then(function(params) {
          console.log(params);
          that.syday = params;
        });
    },
    getsqday() {
      const that = this;
      Contract.methods
        .comLeftDays()
        .call()
        .then(function(params) {
          console.log(params);
          that.comLeftDays = params;
        });
    },
    getcomUnlocked() {
      const that = this;
      Contract.methods
        .comUnlocked()
        .call()
        .then(function(params) {
          console.log(params);
          that.comUnlocked = params;
        });
    },
    getcanGetCom(a) {
      const that = this;
      Contract.methods
        .canGetCom(a)
        .call()
        .then(function(params) {
          console.log(params);
          that.canGetCom = params;
        });
    },
    getCanGetAmount(a) {
      const that = this;
      Contract.methods
        .getCanGetAmount(a)
        .call()
        .then(function(params) {
          console.log(params);
          that.CanGetAmount = params;
        });
    },
    getUnlockedAmount(a) {
      const that = this;
      Contract.methods
        .getUnlockedAmount(a)
        .call()
        .then(function(params) {
          console.log(params);
          that.UnlockedAmount = params;
        });
    },
    getmoney() {
      const that = this;
      that.$toast.loading({
        message: this.$t("common.jzz"),
        forbidClick: true,
        duration: 0
      });
      let SELF_ADDR = sessionStorage.getItem("token");
      Contract.methods
        .userObtain()
        .send({ from: SELF_ADDR })
        .on("receipt", function(res) {
          console.log(res);
          that.$toast.clear();
          that.getpost();
        })
        .on("error", function(error) {
          console.log("领取失败");
          that.$toast.fail(that.$t('common.lqsb'));
        });
    },
    getsociamoney() {
      that.$toast.loading({
        message: this.$t("common.jzz"),
        forbidClick: true,
        duration: 0
      });
      let SELF_ADDR = sessionStorage.getItem("token");
      Contract.methods
        .obtainCom()
        .send({ from: SELF_ADDR })
        .on("receipt", function(res) {
          console.log(res);
          that.$toast.clear();
          that.getpost();
        })
        .on("error", function(error) {
          console.log("领取失败");
          that.$toast.fail(that.$t('common.lqsb'));
        });
    },
    getexchange(){
      const that = this;
      Contract.methods
        .exchangeable()
        .call()
        .then(function(params) {
          console.log(params);
          that.exchange=params
        });
    },
    sure() {
      if (this.type == 0) {
        this.rg();
      } else {
        this.rg1();
      }
    },
    rg() {
      let heyue_addr = this.heyue_addr;
      let SELF_ADDR = sessionStorage.getItem("token");
      let num = this.num.toString();
      const that=this
      let val=web3.utils.toWei(num, 'mwei')
      Contractusdttoken.methods
        .approve(heyue_addr, val)
        .send({ from: SELF_ADDR })
        .on("receipt", function(receipt) {
          console.log("授权成功", receipt);
          this.submit();
        })
        .on("error", function(error) {
          console.log("555");
          that.$toast.fail(that.$t('common.sqsb'));
        });
    },
    rg1() {
      let heyue_addr = this.heyue_addr;
      let SELF_ADDR = sessionStorage.getItem("token");
      let num = this.num.toString();
      const that=this
      let val=web3.utils.toWei(num, 'ether')
      Contractuoratoken.methods
        .approve(heyue_addr, val)
        .send({ from: SELF_ADDR })
        .on("receipt", function(receipt) {
          console.log("授权成功", receipt);
          this.submit();
        })
        .on("error", function(error) {
          console.log("555");
          that.$toast.fail(that.$t('common.sqsb'));
        });
    },
    submit() {
      const that = this;
      if (that.type == 0) {
        let num = this.num * 1000000;
        Contract.methods
          .convertUsdtToUora(num)
          .send({ from: SELF_ADDR })
          .on("receipt", function(res) {
            console.log(res);
            that.showbox = false;
          })
          .on("error", function(error) {
            console.log("555");
            that.$toast.fail(that.$t('common.dhsb'));
          });
      } else {
        let num = this.num * 1000000000000000000;
        Contract.methods
          .convertUoraToUsdt(num)
          .send({ from: SELF_ADDR })
          .on("receipt", function(res) {
            console.log(res);
            that.showbox = false;
          })
          .on("error", function(error) {
            console.log(error);
            that.$toast.fail(that.$t('common.dhsb'));
          });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
