          }
        }
      }
    }
  }
  .wrapbot {
    width: 610px;
    padding: 30px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    margin: 30px auto 180px;
    box-shadow: 0 0 30px 10px #013920;
    .top {
      display: flex;
      align-items: center;
