<template>
  <div class="share">
    <div class="share_top">
      <div class="top">
        <div class="item">
          <div class="num">{{amount}}</div>
          <div class="text">{{$t('share.tjsl')}}</div>
        </div>
        <div class="item">
          <div class="num">{{userinfo.reward/1000000}}</div>
          <div class="text">{{$t('share.tjjl')}}</div>
        </div>
      </div>
      <div class="link">
        <div class="linktext">{{$t('share.wdtjlj')}}</div>
        <div class="linkurl">
          <div class="left">{{url}}</div>
          <div class="right" v-clipboard:copy="url" v-clipboard:success="copy">{{$t('share.fz')}}</div>
        </div>
      </div>
      <div class="invite">
        <div class="left">{{$t('share.wdyqm')}}</div>
        <div class="right">
          <div class="code">{{code}}</div>
          <div class="op" v-clipboard:copy="code" v-clipboard:success="copy">{{$t('share.fz')}}</div>
        </div>
      </div>
    </div>

    <div class="share_bot">
      <div class="text">{{$t('share.yqjl')}}</div>
      <div class="top">
        <span>{{$t('share.yh')}}</span>
        <span>{{$t('share.rgsl')}}</span>
      </div>
      <div class="bot">
        <div v-if="list.length!=0">
          <div v-for="(item,index) in list" class="item" :key="index">
            <span class="spanleft">{{item.addr}}</span>
            <span>{{item.amount}}</span>
          </div>
        </div>
        <div class="nodata" v-if="list.length==0">{{$t('common.zwjl')}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "share",
  data() {
    return {
      code: "",
      loading: false,
      finished: false,
      list: [],
      userinfo: "",
      url: "",
      amount:0,
       heyue_addr:'0xf0e88e6a1e60df6e25fc58432447811020a503db',
    };
  },
  mounted() {
    this.$toast.loading({
      message: this.$t("common.jzz"),
      forbidClick: true,
      duration: 0
    });
    if (Contract) {
      let a = sessionStorage.getItem("token");
      this.getuserinfo(a);
      this.getlist();
      this.getpeople(a)
    } else {
      window.addEventListener("load", async () => {
        if (window.ethereum) {
          window.web3 = new Web3(ethereum);
          try {
            await ethereum.enable();
          } catch (error) {}
        } else if (window.web3) {
          window.web3 = new Web3(web3.currentProvider);
        } else {
          console.log(
            "Non-Ethereum browser detected. You should consider trying MetaMask!"
          );
          window.web3 = new Web3(
            new Web3.providers.HttpProvider(
              "https://api.infura.io/v1/jsonrpc/ropsten"
            )
          );
        }
        web3.eth.getAccounts(function(err,accounts) {
          sessionStorage.setItem("token", accounts);
        });
        let heyue_addr = this.heyue_addr;
        let usdt_heyue = "0xee46026fb343831f0550ca10b5f36dabda34d120";
        let uora_heyue = "0x64eb9594009d2a0714a1313bc8010d94a04e3908";
        Contract = new web3.eth.Contract(abi, heyue_addr); //合约
        Contractusdttoken = new web3.eth.Contract(tokenabi, usdt_heyue); //合约
        Contractuoratoken = new web3.eth.Contract(tokenabi, uora_heyue); //合约
        let a = sessionStorage.getItem("token");
        this.getuserinfo(a);
        this.getlist();
        this.getpeople(a)
      });
    }
  },
  methods: {
    onLoad() {},
    getuserinfo(accounts) {
      const that = this;
      Contract.methods
        .addressToUser(accounts)
        .call()
        .then(function(params) {
          console.log(params);
          that.userinfo = params;
          that.code = params.referCode;
          that.url =
            sessionStorage.getItem("url") + "?code=" + params.referCode;
        });
    },
    copy() {
      this.$toast.success(this.$t("common.fzcg"));
    },
    getlist() {
      const that = this;
      let accounts = sessionStorage.getItem("token");
      Contract.methods
        .getLast10Children(accounts)
        .call()
        .then(function(params) {
          console.log(params);
          that.getamount(params);
        });
    },
    getpeople(a){
      const that=this
             Contract.methods
          .getChildCount(a)
          .call()
          .then(function(res) {
              that.amount=res
          });      
    },
    getamount(params) {
      let list = [];
      for (let i = 0; i < params.length; i++) {
        Contract.methods
          .addressToUser(params[i])
          .call()
          .then(function(res) {
            let obj = {
              addr: params[i],
              amount: res.investAmount / 1000000
            };
            list.push(obj);
          });
      }
      this.$toast.clear();
      this.list = list;
    }
  }
};
</script>
<style lang="scss" scoped>
.share {
  line-height: 1;
  display: flex;
  flex-direction: column;
  height: 100vh;
  .share_top {
    width: 610px;
    padding: 30px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    margin: 30px auto 0px;
    box-shadow: 0 0 30px 10px #013920;
    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px 0;
      border-bottom: 1PX solid rgba(0, 255, 143, 0.8);
      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 50%;
        &:last-child {
          border-left: 1PX solid rgba(0, 255, 143, 0.6);
        }
        .num {
          font-size: 40px;
          font-family: PingFang SC;
          font-weight: bold;
          color: rgba(0, 255, 143, 1);
          margin-bottom: 27px;
        }
        .text {
          font-size: 28px;
          font-family: PingFang SC;
          font-weight: 400;
          color: rgba(188, 255, 226, 1);
        }
      }
    }
    .link {
      padding: 30px 0;
      border-bottom: 1PX solid rgba(0, 255, 143, 0.8);
      .linktext {
        font-size: 36px;
        font-family: PingFang SC;
        font-weight: bold;
        color: rgba(0, 255, 143, 1);
        text-align: left;
        margin-bottom: 30px;
      }
      .linkurl {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
          flex: 1;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 22px;
          font-family: PingFang SC;
          font-weight: bold;
          color: rgba(188, 255, 226, 1);
        }
        .right {
          font-size: 28px;
          font-family: PingFang SC;
          font-weight: bold;
          text-decoration: underline;
          color: rgba(0, 255, 143, 1);
          margin-left: 40px;
        }
      }
    }
    .invite {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 30px 0;
      .left {
        font-size: 36px;
        font-family: PingFang SC;
        font-weight: bold;
        color: rgba(0, 255, 143, 1);
      }
      .right {
        display: flex;
        align-items: center;
        .code {
          font-size: 30px;
          font-family: PingFang SC;
          font-weight: bold;
          color: rgba(188, 255, 226, 1);
        }
        .op {
          font-size: 28px;
          font-family: PingFang SC;
          font-weight: bold;
          text-decoration: underline;
          color: rgba(0, 255, 143, 1);
          margin-left: 40px;
        }
      }
    }
  }
  .share_bot {
    width: 610px;
    padding: 30px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    margin: 30px auto 0px;
    box-shadow: 0 0 30px 10px #013920;
    height: 45vh;
    display: flex;
    flex-direction: column;
    .text {
      font-size: 36px;
      font-family: PingFang SC;
      font-weight: bold;
      color: rgba(0, 255, 143, 1);
      margin-bottom: 34px;
      text-align: left;
    }
    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 28px;
      font-family: PingFang SC;
      font-weight: 400;
      color: rgba(188, 255, 226, 1);
      border-bottom: 1PX solid rgba(0, 255, 143, 0.8);
      padding: 30px 0;
    }
    .bot {
      flex: 1;

      overflow-y: scroll;
      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 28px;
        font-family: PingFang SC;
        font-weight: 400;
        color: rgba(188, 255, 226, 1);
        border-bottom: 1PX solid rgba(0, 255, 143, 0.8);
        padding: 30px 0;
        .spanleft {
          // flex: 1;
          margin-right: 40px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 70%;
        }
      }
      .nodata {
        font-size: 26px;
        color: #ffffff;
        text-align: center;
        margin-top: 200px;
      }
    }
  }
}
</style>